<template>
    <div class="lmui-lookup-field" :style="{width:computedWidth}" @keyup.esc="resetState">
        <div class="lookup-input" :class="{'disabled': isDisabled}" style="position:relative;">
            <input :class="customClassForInput" type="text" style="width:100%;padding-left:10px;" :maxlength="maxlength"
                   @keyup.stop="onInputKeyup" @click="onInputFocus" @blur="onInputBlur" v-model="searchQuery" :placeholder="placeholder" :disabled=isDisabled>
            <svg class="icons26 icon-gray lmui-icon-lookup">
                <use xlink:href="#icon-lookup-26"></use>
            </svg>
            <div class="glob-tip" :class="{'enable': isGlob}" v-if="isGlob">
                <lm-toggle  ref="globToggle" active-color="white"  not-active-text="" :active-text="treatGlobText" v-if="isAllowManuallyChangeGlob"
                            :on-switch="onSwitchGlobToggle" :is-active-at-init="isInitialGlobToggleActive" size="small" v-show="!!searchQuery"></lm-toggle>
                <span class="glob-tip-label" v-show="!searchQuery" style="color: white;">*</span>
                <span class="glob-tip-label" v-show="!!searchQuery" style="color: white;font-size: 12px;
                display: inline-block;margin-top: -4px;" v-if="!isAllowManuallyChangeGlob">{{treatGlobText}}</span>
            </div>
        </div>
        <div class="lookup-menu" v-show="state != 0">
            <div class="lookup-tip" v-show="state == 1">
                <div class="loadingcircles32" style="display:inline-block;position:absolute;top:0;right:10px;">
                    <div :class="'circle32-gray circle32_0' + number" v-for="number in [1,2,3,4,5,6,7,8]"></div>
                </div><span>Searching for suggestions...</span>
            </div>
            <div class="lookup-tip" v-show="state == 2 && displayItems.length > 0">Suggested Results</div>
            <div class="lookup-tip" v-show="state == 2 && displayItems.length == 0">No matching result found</div>
            <div class="look-up-item" :class="{hovering: index == hoveringIndex}" v-for="(item, index) in displayItems" :key="item.id"
                 @mousedown.stop.prevent="onSelectItem" :data-value="JSON.stringify(item)"
             @mouseover="onMouseover" @mouseout="onMouseout" @keyup.enter.stop.prevent="onSelectItem">
                <span v-html="item.iconHtml" v-show="!!item.iconHtml"></span>
                <span v-html="highlightItem(item[displayField], item['displayHandle'])"></span>
            </div>
            <div class="lookup-tip" v-show="state == 2 && items.length > displayItems.length">Keep typing to refine results</div>
        </div>
    </div>
</template>

<script>
    import utils from 'utils';
    import dom from 'dom';
    import '../common/scss/common.scss';
    import './lookup-field.scss';
    import Toggle from '../toggle/toggle';

    export default {
        components: {
            lmToggle: Toggle
        },
        props: {
            /**
             * define the initial raw items for the autocomplete
             *
             * for lookup component, the items should always be objects,
             * if the items passed to the component are not objects, we will automatically convert it to objects
             */
            items: {
                type: [Array],
                default: function () {
                    return [];
                }
            },
            initialSelectedItem: { // the initialSelectedItem is used to track the selected item from the dropdown
                type: [Object],
                default: null
            },
            customClassForInput: { //support pass a custom class to input element
                type: [String],
                default: ''
            },
            width: {
                type: [String, Number],
                default: '100%'
            },
            placeholder: String,        // define the placeholder for the lookup input
            maxlength:  {      // define the limit maxlength for the lookup input
                type: [String, Number],
                default: 1024
            },
            resultLimits: {             // the max items displayed in the lookup dropdown
                type: [Number],
                default: 10
            },
            isDisabled: {
                type: [Boolean],
                default: false
            },

            //glob related begin
            isGlob: {               // define if the lookup enable the glob mode
                type: [Boolean],
                default: false
            },
            isAllowManuallyChangeGlob : { // if user can manually operate toggle to change the glob status
                type: [Boolean],
                default: false
            },
            isInitialGlobToggleActive : {
                type: [Boolean],
                default: true
            },
            //glob related end

            isResetQueryOnFocus: {      // define whether to keep the search query when the input got focus
                type: [Boolean],
                default: true
            },
            isEnableSelectMode: {       // enable select mode, when select some menu item, trigger the "select" event
                type: [Boolean],
                default: false
            },
            isClearQueryAfterSelect: {  // in select mode, define whether to clear the search query after select the menu item
                type: [Boolean],
                default: false
            },

            isFirstAutoGlobItemIgnoreEscape: { // DEV-32931: when user select auto suggestion value, keep the global expression chars for value
                type: [Boolean],
                default: true
            },

            displayField: { // support assign a display field, default is 'name'
                type: [String],
                default: 'name'
            },

            displayOnInputField: { //support display different data to the input field when click item from dropdown items
                type: [String],
                default: ''
            },

            treatGlobText: {
                type: [String],
                default: 'Treat as glob pattern'
            },

            onMounted: {
                type: [Function] // callback on created
            },
            onLookup: {
                type: [Function] // callback on look up triggering
            },
            onValidateQuery: {
                type: [Function], // callback on enter input
                default: function () {
                    return function () {
                        return true;
                    }
                }
            }
        },

        created () {
            //set data from props
            this.selectedItem = this.initialSelectedItem;
            if (this.selectedItem) {    // allow the user to pass a initial selectedItem to set search query
                this.searchQuery = this.selectedItem[this.displayOnInputField || this.displayField];
            }

            this.isGlobToggleActive = this.isGlob && (!this.isAllowManuallyChangeGlob || this.isInitialGlobToggleActive);
        },

        mounted () {
            this.$nextTick(function () {
                if(this.onMounted){
                    this.onMounted();
                }
            });
        },

        data () {
            return {
                searchQuery: '',
                state: 0,               // 0: init, 1: onSearching, 2: showSearchedList,
                hoveringIndex: -1,
                selectedItem: null, //which item been selected
                isGlobToggleActive: true
            }
        },

        computed: {
            isGlobToggleEnabled: function(){ //parent view can use this field to get glob status
                this.isGlobToggleActive = this.isGlob && (!this.isAllowManuallyChangeGlob || this.$refs.globToggle.active || this.searchQuery === '');
                return this.isGlobToggleActive;
            },

            computedWidth: function() {
                return Number.isInteger(Number(this.width)) ? (this.width + 'px'): this.width;
            },
            /**
             *  the display items are only for displaying, it will be limited by the resultLimits
             */
            displayItems: function () {
                let parsedItems = [];
                this.items.forEach(function(item) {
                    if(_.isObject(item)) {
                        if(!item.id) {
                            item.id = JSON.stringify(item);
                        }
                        parsedItems.push(item);

                    } else {
                        parsedItems.push({
                            id:String(item),
                            [this.displayField]:String(item)
                        });
                    }
                }, this);

                if(this.isGlobToggleActive){
                    let globItemQuery = (this.searchQuery || '').trim() + (/\*$/.test(this.searchQuery) ? '' : '*');
                    let globItem = {
                        id: globItemQuery,
                        [this.displayField]: globItemQuery
                    };

                    if (this.isFirstAutoGlobItemIgnoreEscape) {
                        globItem.ignoreEscapeGlobal = true;
                    }

                    parsedItems = [globItem].concat(parsedItems)
                }

                return parsedItems.slice(0, this.resultLimits);
            }
        },

        methods: {
            highlightItem: function (displayItem, displayHandle) {
                let value;
                if(displayHandle){
                    value = displayHandle(displayItem);
                } else {
                    value = displayItem;
                }

                value = utils.escape(value);
                if (this.searchQuery && this.searchQuery.trim()) {
                    return value.replace(new RegExp(utils.escapeRegExp(utils.escape(this.searchQuery)), 'ig'), '<span style="font-weight:normal;color: #000;">$&</span>');
                } else {
                    // fix bug DEV-32717
                    return value;
                }
            },

            onSwitchGlobToggle (isActive) {
                this.$emit('globchanged', this.isGlobToggleEnabled);
            },

            onInputKeyup (e) {
                switch(e.keyCode) {
                    case 13: // enter
                        this.onInputEnter();
                        break;
                    case 27: // esc
                        this.resetState();
                        break;
                    case 40: // up or down arrow
                    case 38:
                        this.doKeyboardNav(e.keyCode);
                        break;
                    case 9:  // check the tab key to do the focus event
                        this.onInputFocus();
                        break;
                    default:
                        //auto active glob toggle
                        if(this.isGlob && this.isAllowManuallyChangeGlob && (/[\*\?]+/.test(this.searchQuery) || this.searchQuery == '')) {
                            this.$refs.globToggle.active = true;
                            this.$emit('globchanged', this.isGlobToggleEnabled);
                            this.isGlobToggleActive = true;
                        }
                        this.doLookup(this.searchQuery);
                }
            },

            /**
             * call this function if the input got focus or get clicked
             * why don't directly listen to the "focus" event?
             * because we also need to listen to the "click" event, and the "click" event will cause "focus" event,
             * that will make the onInputFocus get called duplicatedly
             */
            onInputFocus () {
                this.hoveringIndex = -1;
                this.doLookup(this.isResetQueryOnFocus ? '' : this.searchQuery);
            },

            doLookup : utils.debounce(function (query) {
                //DEV-22662, if put the logic in attached method, there was a bug in SVG icon of IE
                 //dynamic set lookup menu width
                let $el = $(this.$el);
                $el.find('.lookup-menu').width($el.width() - 30);

                this.state = 1;
                this._initialQuery = query;

                if(this.onLookup){
                    const me = this;
                    this.onLookup(query, function() {
                        if (me.state == 1) {
                            me.state = 2;
                        }
                    });
                } else {
                    this.$emit('lookup', query); //deprecated, use onLookup callback instead
                }
            }, 200),

            doKeyboardNav (keyCode) {
                if (this.displayItems.length > 0) {
                    if (keyCode == 38) {         // up arrow
                        if (this.hoveringIndex < 0) {
                            this.hoveringIndex = this.displayItems.length - 1;
                        } else {
                            --this.hoveringIndex;
                        }
                    } else if (keyCode == 40) {  // down arrow
                        ++this.hoveringIndex;

                        if (this.hoveringIndex >= this.displayItems.length) {
                            this.hoveringIndex = -1;
                        }
                    }

                    if (this.hoveringIndex > -1) {
                        this.searchQuery = this.displayItems[this.hoveringIndex][this.displayField];
                    } else if (this.hoveringIndex == -1) {
                        this.searchQuery = this._initialQuery;
                    }
                }
            },

            onSelectItem (e) {
                let item = utils.parseJSON(dom.getElData(e.currentTarget, 'value'));

                //auto active glob toggle
                if(this.isGlob && this.isAllowManuallyChangeGlob && !/[\*|\?]+/.test(typeof item == 'object' ? item.name : item)){
                    if(this.isAllowManuallyChangeGlob){
                        this.$refs.globToggle.active = false;
                        this.$emit('globchanged', this.$refs.globToggle.active);
                    }
                    this.isGlobToggleActive = false;
                }
                this.updateSelectItem(item);
            },

            updateSelectItem (item) {
                // update the selected item when the user select from the dropdown
                this.selectedItem = item;

                // do not validate when the user select the item from the dropdown
                clearTimeout(this.blurTimer);

                this.selectItem(this.selectedItem);
            },

            selectItem (item) {
                if(this.isGlobToggleActive && this.selectedItem && !item.ignoreEscapeGlobal) {
                    this.selectedItem[this.displayField] = this.selectedItem[this.displayField]
                        .replace(/\\/g, '\\\\')
                        .replace(/\(/g, '\\(')
                        .replace(/\)/g, '\\)')
                        .replace(/\[/g, '\\[')
                        .replace(/]/g, '\\]')
                        .replace(/\|/g, '\\|')
                        .replace(/!/, '\\!');
                }

                let selectedItem = item;

                if (!_.isObject(selectedItem)) {
                    let searchQuery = String(item);
                    selectedItem = {
                        id: searchQuery,
                        [this.displayField]: searchQuery
                    };
                }

                this.searchQuery = selectedItem[this.displayOnInputField || this.displayField];
                this.resetState();

                if (this.isEnableSelectMode) {
                    this.$emit('select', selectedItem);

                    if (this.isClearQueryAfterSelect) {
                        this.searchQuery = '';

                        // need clear the selectedItem when clear the search query
                        this.selectedItem = null;
                    }
                }
            },

            validateSelectedItem (isQueryRequired) {
                return (this.searchQuery && this.selectedItem && this.selectedItem[this.displayOnInputField || this.displayField] == this.searchQuery) ||
                        (!isQueryRequired && !this.selectedItem && !this.searchQuery);
            },

            resetState () {
                this.hoveringIndex = -1;
                this._initialQuery = '';
                this.state = 0;
            },

            onMouseover (e) {
                let curItem = utils.parseJSON(dom.getElData(e.currentTarget,'value'));
                let me = this;
                this.hoveringIndex = this.displayItems.findIndex(function (item) {
                    return item.id == curItem.id && item[me.displayField] == curItem[me.displayField];
                });
            },

            onMouseout (e) {
                this.hoveringIndex = -1;
            },

            onInputEnter () {
                let query = (this.searchQuery || '').trim();

                if (query && this.onValidateQuery(query)) {
                    if (this.hoveringIndex > -1 && this.hoveringIndex < this.displayItems.length) {
                        this.selectedItem = this.displayItems[this.hoveringIndex];
                        this.selectItem(this.selectedItem);
                    } else {
                        this.selectItem({
                            id: query,
                            [this.displayField]: query
                        });
                    }
                }
            },

            onInputBlur () {
                const me = this;

                me.$emit('inputblur', me.searchQuery);
                // if the selectItem is executed after the blur event, then we need have a chance to cancel the blur events
                this.blurTimer = setTimeout(function() {
                    me.resetState();
                    me.$emit('validate', me.searchQuery);
                }, 100);
            }
        },

        watch: {
            'items': function (val, oldVal) {
                if(!this.onLookup){
                    if (this.state == 1) {
                        this.state = 2;
                    }
                }
            },
            'selectedItem': function (val, oldVal) {
                this.$emit('updateSelectedItem', val);
            }
        }
    }
</script>



