<template>
    <div class="lmui-multi-lookup" :style="{width:normalizedWidth}">
        <lookup-field :is-glob="isGlob" :is-glob-dongle-visible="false" :items="items"
                      :is-clear-query-after-select="isClearQueryAfterSelect"
                      :is-reset-query-on-focus="isResetQueryOnFocus"
                      @lookup="doQuery" @select="doAdd" :placeholder="placeholder"
                      :is-enable-select-mode="isEnableSelectMode" :on-validate-query="onValidateQuery"
                      @validate="doValidate" :width="normalizedWidth" ref="lookup" :initial-selected-item="selectedItem"
                      @update-selected-item="updateSelectedItem"></lookup-field>
        <transition-group tag="div" name="lmui-staggered" class="multi-selected-items"
                          :class="{empty: !selectedItems.length}"
                          v-on:after-enter="afterEnter"
                          v-on:after-leave="afterLeave"
                          style="">
            <div class="multi-selected-item" v-for="item in selectedItems" stagger="100" :key="item.id"
                 :data-value="JSON.stringify(item)">
                <span class="item-text">{{item.name}}</span>
                <svg class="icons18 icon-gray lmui-icon-cancel" @click="doDelete">
                    <use xlink:href="#icon-cancel"></use>
                </svg>
            </div>
        </transition-group>
    </div>
</template>

<script>
    import '../common/scss/common.scss';
    import './multi-lookup.scss';
    import LookupField from '../lookup-field/lookup-field';
    import utils from 'utils';
    import dom from 'dom';

    var MultiLookup = {
        props: {
            isGlob: Boolean,             // define if the multi lookup enable the glob mode
            items: Array,                // define the initial items for the autocomplete
            width: {                     // define the width of the multi lookup, default is 100%
                type: [String, Number],
                default: '100%'
            },
            placeholder: String,         // define the placeholder for the lookup input
            isResetQueryOnFocus: {       // define whether to keep the search query when the input got focus
                type: [Boolean],
                default: true
            },
            isEnableSelectMode: {       // enable select mode, when select some menu item, trigger the "select" event
                type: [Boolean],
                default: true
            },
            isClearQueryAfterSelect: {  // in select mode, define whether to clear the search query after select the menu item
                type: [Boolean],
                default: true
            },
            initialSelectedItems: {            // define the selected items
                type: [Array],
                default: function () {
                    return [];
                }
            },
            selectedItem: null,
            canAddItem: {
                type: [Function],
                default: function () {
                    return function () {
                        return true;
                    }
                }
            },
            onValidateQuery: {
                type: [Function],
                default: function () {
                    return function () {
                        return true;
                    }
                }
            }
        },

        computed: {
            normalizedWidth () {
                var width = this.width;
                return Number.isInteger(Number(width)) ? (width + 'px') : width;
            }
        },

        data () {
            return {
                selectedItems: [],
                isGlobExpanded: false
            };
        },

        created () {
            var arr = this.initialSelectedItems;

            arr.forEach(function (item, index) {
                if (!_.isObject(item)) {
                    this.selectedItems[index] = {
                        id: String(item),
                        name: String(item)
                    };
                } else {
                    this.selectedItems[index] = item;

                }

                var curItem = this.selectedItems[index];

                if (!curItem.id) {
                    curItem.id = JSON.stringify(curItem);
                }
            }, this);
        },

        mounted() {
            this._resetGlobToolTipPosition();
        },

        methods: {
            doQuery (query) {
                this.$emit('lookup', query);
            },

            doAdd (item) {
                // when the user select the text, will also trigger a select event
                // we need to filter out these events
                if (_.isObject(item) && item.type == 'select' && item.target) {
                    return;
                }

                if (!this.canAddItem(item)) {
                    return;
                }

                if (!this.selectedItems.find(function (curItem) {
                            return curItem.id == item.id;
                        })) {
                    this.selectedItems.push(item);
                    this.$emit('add', item);

                    // allow the user to config some common behaviors for all instances of the the MultiLookup
                    if (MultiLookup.doAdd) {
                        var me = this;
                        // because of the staggered delay is 100ms, here we use 150ms delay for trigger the class level events
                        setTimeout(function () {
                            MultiLookup.doAdd(me, item);
                        }, 150);
                    }
                }
            },

            doDelete (event) {
                const closetEl = dom.getClosetEl(event.currentTarget, '.multi-selected-item');
                var toDelItem = utils.parseJSON(dom.getElData(closetEl, 'value'));

                this.selectedItems = this.selectedItems.filter(function (item) {
                    return item.id != toDelItem.id;
                });

                this.$emit('delete', toDelItem);

                // allow the user to config some common behaviors for all instances of the the MultiLookup
                if (MultiLookup.doDelete) {
                    var me = this;
                    // because of the staggered delay is 100ms, here we use 150ms delay for trigger the class level events
                    setTimeout(function () {
                        MultiLookup.doDelete(me, item);
                    }, 150);
                }

                this.doValidate();
            },

            doValidate () {
                this.$emit('validate', this.$refs.lookup.searchQuery);
            },

            toggleGlobTip () {
                this.isGlobExpanded = !this.isGlobExpanded;
            },

            validateSelectedItem () {
                return this.$refs.lookup.validateSelectedItem();
            },

            updateSelectedItem (selectedItem) {
                this.$emit('updateSelectedItem', selectedItem);
            },

            //DEV-29031  why isn't there a toggle and why did we attach it to the top of the field, instead of underneath
            _resetGlobToolTipPosition() {
                const me = this;
                this.$nextTick(function () {
                    setTimeout(function () {
                        let elHeight = $(me.$el).outerHeight();
                        $(me.$el).find('.glob-tip').css('top', elHeight < 35 ? 35 : elHeight);
                    }, 50);
                });
            },

            afterEnter(el) {
                this.$emit('select-item-change', 'enter', el);
                this._resetGlobToolTipPosition();
            },

            afterLeave(el) {
                this.$emit('select-item-change', 'leave', el);
                this._resetGlobToolTipPosition();
            }
        },

        components: {
            lookupField: LookupField
        }
    };

    export default MultiLookup;
</script>



